/**
 * UA 
 *
 * 
 *
 * {
 *    name: 'mac',     //  name  switch
 *    mac: true,       //  if (mac) { ... }
 *    version: '8.0'   // string 
 * }
 *
 */

import { OS } from '../types/type'
import { each } from '../util/array'

/**
 * linux  Linux 
 */

//  PC 
const list = [
  [ 'harmony', /harmonyos ([\d_.]+)/ ],
  [ 'harmony', /openharmony ([\d_.]+)/ ],
  [ 'iphone', /iphone os ([\d_.]+)/ ],
  [ 'ipad', /ipad; cpu os ([\d_.]+)/ ],
  [ 'itouch', /itouch; cpu os ([\d_.]+)/ ],
  [ 'android', /android ([\d_.]+)/ ],
  [ 'wp', /windows phone ([\d_.]+)/ ],
  [ 'windows', /windows nt ([\d_.]+)/ ],
  [ 'linux', /linux/ ],
  [ 'mac', /mac os x ([\d_.]+)/ ]
]

const iosMap = {
  iphone: 1,
  ipad: 1,
  itouch: 1
}

/**
 *  UA 
 *
 * @inner
 * @param {string} ua
 * @return {Object}
 */
function parseUA(ua: string): OS {

  let name: string
  let version: string

  each(
    list,
    (item) => {
      let match = (item[1] as RegExp).exec(ua)
      if (match) {
        name = item[0] as string
        version = match[1]
        if (version) {
          version = version.replace(/_/g, '.')
        }
        return false
      }
    }
  )

  return {
    name: name || '',
    version: version || '',
    mobile: ua.indexOf('mobile') > -1
  }
}

const os = parseUA((typeof navigator === 'object' && navigator.userAgent || '').toLowerCase())
if (os.name) {
  os[os.name] = true
  if (iosMap[os.name]) {
    os.ios = true
  }
}

export default os
