/**
 * @file 
 */

import execute from './execute'
import { Fn } from '../types/type'
import * as array from '../util/array'
import * as constant from '../util/constant'
import { ParamType } from '../types/advanced'

/**
 * 
 *
 * @param fn 
 * @param delay 
 * @param immediate 
 * @return 
 */
export default function debounce<T extends Fn>(fn: T, delay: number, immediate?: boolean, last: boolean = false): T {

  let timer: any
  let args: any

  return function () {

    let context = this

    if (last) {
      args = array.toArray(arguments)
    }

    if (!timer) {
      if (!last) {
        args = array.toArray(arguments)
      }
      if (immediate) {
        execute(fn, context, args as ParamType<T>)
      }

      timer = setTimeout(
        function () {
          timer = constant.UNDEFINED
          if (!immediate) {
            execute(fn, context, args as ParamType<T>)
          }
        },
        delay
      )
    }
  } as T
}
