@struct
export class Rational {
  /**
   * 
   */
  num: int32 = 1
  /**
   * 
   */
  den: int32 = 1

  constructor(init?: {den: number, num: number}) {
    if (init) {
      this.den = init.den
      this.num = init.num
    }
  }
}
