/**
 * @file  native 
 */

import * as is from '../util/is'
import toString from './toString'
import * as string from '../util/string'

/**
 *  native 
 * 
 * @param target 
 */
export default function isNative(target: any): boolean {
  return is.func(target) && string.has(toString(target), '[native code]')
}
