export const enum AVDictFlags {
  MATCH_CASE = 1,
  IGNORE_SUFFIX = 2,
  DONT_OVERWRITE = 16,
  APPEND = 32,
  MULTIKEY = 64
}

@struct
export class AVDictionaryEntry {
  key: pointer<void> = nullptr
  value: pointer<void> = nullptr
}

@struct
export class AVDictionary {
  count: int32 = 0
  elems: pointer<AVDictionaryEntry> = nullptr
}
